@import "./Utilities/Variables";
@import "./Utilities//Mixins";
@import "./Utilities//BreakPoints";

.parentContainer {
  min-height: 100vh;
  padding-top: 200px;
  @include d-flex(center, start, row, 0px);
}

.mainContainer {
  width: 60%;
  background-color: $color-primary;
}

.header{
  text-align: center;
  padding: 20px 0;  
  border-bottom: 2px solid white
}

.personalSpace {
  @include d-flex(start, center, column, 0px);
  padding: 20px 0;
}

.imgContainer {
  width: 35%;
  padding-bottom: 20px;
  position: relative;

  &__userImg {
    width: 100%;
    height: 250px;
    border-radius: 8px;
  }

  &__changeImg {
    width: 40px;
    height: 40px;
    background-color: #2e3830;
    color: white;
    @include d-flex(center, center, row, 0px);
    // opacity: 0.5;
    position: absolute;
    top: 73%;
    left: 80%;
    border-radius: 50%;
    border: 2px solid white;
    cursor: pointer;
  }

  &__changeImg :hover {
    color: green;
    font-size: large;
    transition: 1s;
  }

  &__errorMessage {
    font-size: small;
    color: crimson;
  }
}

.imgInput {
  display: none;
}

.personalInfo {
  width: 100%;
  background-color: aqua;
}

.userInfo {
  background-color: $color-secondary;
  @include d-flex(start, center, row, 0px);
  border-bottom: 1px solid white;
  border-top: 1px solid white;

  &__keyBox,
  &__valueBox {
    width: 50%;
    padding: 10px 0 10px 10%;
    font-size: 15px;

    &__readOnlyInput,
    &__editInput {
      width: 260px;
      height: 40px;
      border: none;
      font-size: 15px;
      margin-right: 5px;
      border-radius: 5px;
      margin-right: 5px;
      padding-left: 5px;
    }

    &__readOnlyInput {
      background-color: transparent;
    }

    &__editInput {
      background-color: #e8f0fe;
    }

    &__readOnlyInput:focus,
    &__editInput:focus {
      outline: none;
    }

    &__errorMessage{
        font-size: small;
        color: crimson;
    }

    &__relative{
      position: relative;
    }

    &__moreInfoText{
      width: 260px;
      background-color: rebeccapurple;
    }
  }
}

.faCircleCheck {
  color: green;
  margin-right: 5px;
}

.faCircleXmark {
  color: red;
  margin-right: 5px;
}

.faPen {
  color: white;
  cursor: pointer;
}

.faEye{
position: absolute;
top: 30%;
left: 63%;
cursor: pointer;
}
